import '../stylesheets/pokemon.css';

function Pokemon(props){
    const fichasPokemon = props.data.types.map((types, i)=> {
  
        return (
        <li key={i} poketype="types" className="pokeListTypes">
            {types}</li>
    )}
    )
    return(
        <article className="PokeCard">
            <p className="pokeId">{/* <img src="https://www.dlf.pt/dfpng/middlepng/597-5970982_pokeball-pixel-png-transparent-png.png"></img> */} {"00" +props.data.id}</p>
        <img src={props.data.url}/>
       <h2 className="pokename">{props.data.name}</h2>

       <ul className="poketype">
       {fichasPokemon}
        </ul>
    
        </article>
    )
}


export default Pokemon;